import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20182 from "../../assets/2018/20180810_093312.jpg";
import photo20183 from "../../assets/2018/20180810_093316.jpg";
import photo20184 from "../../assets/2018/SAM_0532.jpg";
import photo20186 from "../../assets/2018/SAM_0534.jpg";
import photo20185 from "../../assets/2018/SAM_0538.jpg";
import photo20181 from "../../assets/2018/20180805_104649.jpg";
import ImageViewer from "../ImageViewer";

// Видео сюда

const Category2018 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2018</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo20182}/>
                    <ImageViewer src={photo20183}/>
                    <ImageViewer src={photo20184}/>
                    <ImageViewer src={photo20186}/>
                    <ImageViewer src={photo20185}/>
                    <ImageViewer src={photo20181}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2018;