import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20171 from "../../assets/2017/20170803_174803.jpg";
import VideoPlayer from "../VideoPlayer";
import ImageViewer from "../ImageViewer";

// Видео сюда
const video20171 = require('../../assets/2017/20170803_174844.mp4');
const video20172 = require('../../assets/2017/20170804_192044.mp4');
const video20173 = require('../../assets/2017/20170806_115700.mp4');

const Category2017 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2017</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 2]}>
                    <ImageViewer src={photo20171}/>

                    <VideoPlayer videoUrl={video20171}/>
                    <VideoPlayer videoUrl={video20172} />
                    <VideoPlayer videoUrl={video20173} />
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2017;