import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20081 from "../../assets/2008/DCFC0002.jpg";
import photo20082 from "../../assets/2008/DCFC0003.jpg";
import photo20083 from "../../assets/2008/DCFC0004.jpg";
import photo20084 from "../../assets/2008/AA001A.jpg";
import photo20085 from "../../assets/2008/AA003A.jpg";
import photo20086 from "../../assets/2008/AA004A.jpg";
import photo20087 from "../../assets/2008/AA005A.jpg";
import photo20088 from "../../assets/2008/AA006A.jpg";
import photo20089 from "../../assets/2008/AA007A.jpg";
import photo200810 from "../../assets/2008/AA008A.jpg";
import photo200811 from "../../assets/2008/AA__XA.jpg";
import photo200812 from "../../assets/2008/AA_0000A.jpg";
import ImageViewer from "../ImageViewer";


// Видео сюда

const Category2008 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2008</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                        <ImageViewer src={photo20081}/>
                        <ImageViewer src={photo20082}/>
                        <ImageViewer src={photo20083}/>
                        <ImageViewer src={photo200812}/>
                        <ImageViewer src={photo20084}/>
                        <ImageViewer src={photo20085}/>
                        <ImageViewer src={photo20086}/>
                        <ImageViewer src={photo20087}/>
                        <ImageViewer src={photo200810}/>
                        <ImageViewer src={photo20089}/>
                        <ImageViewer src={photo200811}/>
                        <ImageViewer src={photo20088}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2008;