import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20061 from "../../assets/2006/AA016A.jpg"
import photo20062 from "../../assets/2006/AA018.jpg"
import photo20063 from "../../assets/2006/AA019.jpg"
import photo20064 from "../../assets/2006/AA020.jpg"
import photo20065 from "../../assets/2006/AA021.jpg"
import photo20066 from "../../assets/2006/AA022.jpg"
import photo20067 from "../../assets/2006/AA023.jpg"
import photo20068 from "../../assets/2006/AA024.jpg"
import photo20069 from "../../assets/2006/AA025.jpg"
import photo200610 from "../../assets/2006/AA025A.jpg"
import photo200611 from "../../assets/2006/AA028.jpg"
import ImageViewer from "../ImageViewer";

// Видео сюда

const Category2006 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2006</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo20061}/>
                    <ImageViewer src={photo20062}/>
                    <ImageViewer src={photo20063}/>
                    <ImageViewer src={photo20064}/>
                    <ImageViewer src={photo20065}/>
                    <ImageViewer src={photo20066}/>
                    <ImageViewer src={photo20067}/>
                    <ImageViewer src={photo20068}/>
                    <ImageViewer src={photo20069}/>
                    <ImageViewer src={photo200610}/>
                    <ImageViewer src={photo200611}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2006;