import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";
import ImageViewer from "../ImageViewer";

// Фотографии сюда
import photo200x01 from "../../assets/200x/AA__XA.jpg"
import photo200x02 from "../../assets/200x/AA000A.jpg"
import photo200x03 from "../../assets/200x/AA001A.jpg"
import photo200x04 from "../../assets/200x/AA002A.jpg"
import photo200x05 from "../../assets/200x/AA003A.jpg"
import photo200x06 from "../../assets/200x/AA004A.jpg"
import photo200x07 from "../../assets/200x/AA005A.jpg"
import photo200x08 from "../../assets/200x/AA006A.jpg"
import photo200x09 from "../../assets/200x/AA007A.jpg"
import photo200x10 from "../../assets/200x/AA008A.jpg"
import photo200x11 from "../../assets/200x/AA009A.jpg"
import photo200x12 from "../../assets/200x/AA010A.jpg"
import photo200x13 from "../../assets/200x/AA011A.jpg"
import photo200x14 from "../../assets/200x/AA012A.jpg"
import photo200x15 from "../../assets/200x/AA013A.jpg"
import photo200x16 from "../../assets/200x/AA014A.jpg"
import photo200x17 from "../../assets/200x/AA015A.jpg"
import photo200x18 from "../../assets/200x/AA016A.jpg"
import photo200x19 from "../../assets/200x/AA017A.jpg"
import photo200x20 from "../../assets/200x/AA018A.jpg"
import photo200x22 from "../../assets/200x/AA020A.jpg"
import photo200x23 from "../../assets/200x/AA021A.jpg"
import photo200x24 from "../../assets/200x/AA022A.jpg"
import photo200x25 from "../../assets/200x/AA_0000A.jpg"

// Видео сюда

const Category200X = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.01, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="370px">
                <Text mb="15px" fontSize={["4xl", "6xl"]} letterSpacing="tight" fontWeight="700" color="white">Начало 2000х</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo200x02}/>
                    <ImageViewer src={photo200x03}/>
                    <ImageViewer src={photo200x04}/>
                    <ImageViewer src={photo200x05}/>
                    <ImageViewer src={photo200x06}/>
                    <ImageViewer src={photo200x01}/>
                    <ImageViewer src={photo200x23}/>
                    <ImageViewer src={photo200x08}/>
                    <ImageViewer src={photo200x09}/>
                    <ImageViewer src={photo200x10}/>
                    <ImageViewer src={photo200x12}/>
                    <ImageViewer src={photo200x13}/>
                    <ImageViewer src={photo200x14}/>
                    <ImageViewer src={photo200x15}/>
                    <ImageViewer src={photo200x16}/>
                    <ImageViewer src={photo200x11}/>
                    <ImageViewer src={photo200x17}/>
                    <ImageViewer src={photo200x22}/>
                    <ImageViewer src={photo200x18}/>
                    <ImageViewer src={photo200x19}/>
                    <ImageViewer src={photo200x20}/>
                    <ImageViewer src={photo200x07}/>
                    <ImageViewer src={photo200x24}/>
                    <ImageViewer src={photo200x25}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category200X;