import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20191 from "../../assets/2019/20190803_134149.jpg";
import photo20192 from "../../assets/2019/SAM_0700.jpg";
import photo20193 from "../../assets/2019/SAM_0702.jpg";
import photo20194 from "../../assets/2019/SAM_0706.jpg";
import photo20195 from "../../assets/2019/SAM_0707.jpg";
import photo20196 from "../../assets/2019/SAM_0708.jpg";
import ImageViewer from "../ImageViewer";

// Видео сюда

const Category2019 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2019</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo20191}/>
                    <ImageViewer src={photo20192}/>
                    <ImageViewer src={photo20193}/>
                    <ImageViewer src={photo20194}/>
                    <ImageViewer src={photo20195}/>
                    <ImageViewer src={photo20196}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2019;