import {useState} from 'react';
import { Image, Modal, ModalOverlay, ModalContent, ModalCloseButton, ModalBody } from '@chakra-ui/react';
import "../App.css"
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch"


const ImageViewer = ({ src}: {src: string}) => {
    const [isOpen, setIsOpen] = useState(false);

    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };


    return (
        <>
            <div className="collage-item" onClick={openModal}>
                <Image borderRadius="15px" src={src} className="collage-image" onClick={openModal} cursor="pointer" />
            </div>

            <Modal isOpen={isOpen} onClose={closeModal} size="6xl">
                <ModalOverlay />
                <ModalContent bg="#222222" color="white">
                    <ModalBody>
                        <TransformWrapper>
                            <TransformComponent>
                                <Image objectFit="contain" src={src} alt="Image" maxWidth="100%" maxHeight="100%" margin="0 auto" />
                            </TransformComponent>
                        </TransformWrapper>
                    </ModalBody>
                </ModalContent>
            </Modal>
        </>
    );
};

export default ImageViewer;