import React from "react"
import {useInView} from "react-intersection-observer";
import { motion } from 'framer-motion';
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20051 from "../../assets/2005/AA012A.jpg"
import photo20052 from "../../assets/2005/AA013A.jpg"
import photo20053 from "../../assets/2005/AA014A.jpg"
import photo20054 from "../../assets/2005/AA015A.jpg"
import photo20055 from "../../assets/2005/AA016A.jpg"
import photo20056 from "../../assets/2005/AA017A.jpg"
import photo20057 from "../../assets/2005/AA018A.jpg"
import photo20058 from "../../assets/2005/AA019A.jpg"
import photo20059 from "../../assets/2005/AA020A.jpg"
import photo200510 from "../../assets/2005/AA021A.jpg"
import photo200511 from "../../assets/2005/AA022A.jpg"
import photo200512 from "../../assets/2005/AA023A.jpg"
import photo200513 from "../../assets/2005/AA024A.jpg"
import photo200514 from "../../assets/2005/AA025A.jpg"
import ImageViewer from "../ImageViewer";

// Видео сюда

const Category2005 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2005</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo20051}/>
                    <ImageViewer src={photo20052}/>
                    <ImageViewer src={photo20053}/>
                    <ImageViewer src={photo20054}/>
                    <ImageViewer src={photo20055}/>
                    <ImageViewer src={photo20056}/>
                    <ImageViewer src={photo20057}/>
                    <ImageViewer src={photo20058}/>
                    <ImageViewer src={photo20059}/>
                    <ImageViewer src={photo200510}/>
                    <ImageViewer src={photo200511}/>
                    <ImageViewer src={photo200512}/>
                    <ImageViewer src={photo200513}/>
                    <ImageViewer src={photo200514}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2005;