import React from "react"
import {useInView} from "react-intersection-observer";
import {motion} from "framer-motion";
import {Box, SimpleGrid, Text} from "@chakra-ui/react";

// Фотографии сюда
import photo20151 from "../../assets/2015/SAM_0223.jpg";
import photo20152 from "../../assets/2015/SAM_0224.jpg";
import photo20153 from "../../assets/2015/SAM_0225.jpg";
import photo20154 from "../../assets/2015/SAM_0226.jpg";
import photo20155 from "../../assets/2015/SAM_0234.jpg";
import photo20156 from "../../assets/2015/SAM_0235.jpg";
import photo20157 from "../../assets/2015/SAM_0236.jpg";
import photo20158 from "../../assets/2015/SAM_0237.jpg";
import photo20159 from "../../assets/2015/SAM_0238.jpg";
import photo201510 from "../../assets/2015/SAM_0239.jpg";
import photo201511 from "../../assets/2015/SAM_0240.jpg";
import photo201512 from "../../assets/2015/SAM_0253.jpg";
import photo201513 from "../../assets/2015/SAM_0256.jpg";
import photo201514 from "../../assets/2015/SAM_0260.jpg";
import photo201515 from "../../assets/2015/SAM_0264.jpg";
import photo201516 from "../../assets/2015/SAM_0265.jpg";
import photo201517 from "../../assets/2015/SAM_0271.jpg";
import ImageViewer from "../ImageViewer";

// Видео сюда

const Category2015 = () => {
    const [ref, inView] = useInView({
        triggerOnce: true, // Анимация проигрывается только один раз при появлении в области видимости
        threshold: 0.1, // Порог видимости, при котором срабатывает анимация (от 0 до 1)
    });

    const variants = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0 },
    };

    return (
        <motion.div
            ref={ref}
            initial="hidden"
            animate={inView ? 'visible' : 'hidden'}
            variants={variants}
            transition={{ duration: 0.5 }}
        >
            <Box mt="50px">
                <Text mb="15px" fontSize="6xl" letterSpacing="tight" fontWeight="700" color="white">2015</Text>
                <SimpleGrid maxW={["360px", "1500px"]} spacing="4" columns={[1, 3]}>
                    <ImageViewer src={photo20151}/>
                    <ImageViewer src={photo20152}/>
                    <ImageViewer src={photo20153}/>
                    <ImageViewer src={photo20154}/>
                    <ImageViewer src={photo20155}/>
                    <ImageViewer src={photo20156}/>
                    <ImageViewer src={photo20157}/>
                    <ImageViewer src={photo20158}/>
                    <ImageViewer src={photo20159}/>
                    <ImageViewer src={photo201510}/>
                    <ImageViewer src={photo201511}/>
                    <ImageViewer src={photo201512}/>
                    <ImageViewer src={photo201513}/>
                    <ImageViewer src={photo201514}/>
                    <ImageViewer src={photo201515}/>
                    <ImageViewer src={photo201516}/>
                    <ImageViewer src={photo201517}/>
                </SimpleGrid>
            </Box>
        </motion.div>
    );
}

export default Category2015;